	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import xuexiaoxinxi from '@/views/xuexiaoxinxi/list'
	import discussruxuexuzhi from '@/views/discussruxuexuzhi/list'
	import zaixianjiaoliu from '@/views/zaixianjiaoliu/list'
	import ziliaofenxiang from '@/views/ziliaofenxiang/list'
	import discussxiaoguixinxi from '@/views/discussxiaoguixinxi/list'
	import xiaoguixinxi from '@/views/xiaoguixinxi/list'
	import storeup from '@/views/storeup/list'
	import forum from '@/views/forum/list'
	import ziliaoleixing from '@/views/ziliaoleixing/list'
	import ruxuexuzhi from '@/views/ruxuexuzhi/list'
	import yonghu from '@/views/yonghu/list'
	import chat from '@/views/chat/list'
	import config from '@/views/config/list'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/xuexiaoxinxi',
			name: '',
			component: xuexiaoxinxi
		}
		,{
			path: '/discussruxuexuzhi',
			name: '',
			component: discussruxuexuzhi
		}
		,{
			path: '/zaixianjiaoliu',
			name: '',
			component: zaixianjiaoliu
		}
		,{
			path: '/ziliaofenxiang',
			name: '',
			component: ziliaofenxiang
		}
		,{
			path: '/discussxiaoguixinxi',
			name: '',
			component: discussxiaoguixinxi
		}
		,{
			path: '/xiaoguixinxi',
			name: '',
			component: xiaoguixinxi
		}
		,{
			path: '/storeup',
			name: '',
			component: storeup
		}
		,{
			path: '/forum',
			name: '',
			component: forum
		}
		,{
			path: '/ziliaoleixing',
			name: '',
			component: ziliaoleixing
		}
		,{
			path: '/ruxuexuzhi',
			name: '',
			component: ruxuexuzhi
		}
		,{
			path: '/yonghu',
			name: '',
			component: yonghu
		}
		,{
			path: '/chat',
			name: '',
			component: chat
		}
		,{
			path: '/config',
			name: '',
			component: config
		}
		]
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
